import React, { useEffect, useState } from 'react';
import Close from '@material-ui/icons/Close';
import './CoinGraph.css';
import { Bar, Line } from 'react-chartjs-2';
const data = {
    labels: ["January", "February", "March", "April", "May", "June", "July"],
    datasets: [
      {
        label: "My First dataset",
        fill: false,
        lineTension: 0.1,
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderCapStyle: "butt",
        borderDashOffset: 0.0,
        borderJoinStyle: "miter",
        pointBorderColor: "rgba(75,192,192,1)",
        pointBackgroundColor: "#fff",
        pointBorderWidth: 1,
        pointHoverRadius: 6,
        pointHoverBackgroundColor: "rgba(75,192,192,1)",
        pointHoverBorderColor: "rgba(220,220,220,1)",
        pointHoverBorderWidth: 3,
        pointRadius: 1,
        pointHitRadius: 10,
        data: [65, 59, 80, 81, 56, 55, 40]
      }
    ]
  };

const CoinGraph = ({ openGraph, pickData, closeGraph }) => {
    const [close, setClose] = useState(false)
    useEffect(() => {
        var element = document.getElementById("test");

        element.scrollIntoView({ behavior: 'smooth' });
    }, []);
    const closeGraphCard = (e) => {
        setClose(true);
        setTimeout(() => {
            closeGraph();
        }, 1000)
    }
    return (

        <div className="coinGraph-container">
            <div id="test" className={close === false ? "scale-in-center" : "roll-out-left"}>{pickData}
                <button className="left-exit" onClick={(e) => closeGraphCard(e)}>
                    <Close />
                </button>
                <Line
                    
                    data={data}
                    options={{
                        responsive: true,
                        maintainAspectRatio: false,
                        title: {
                            display: true,
                            text: "מס הגעות",
                            fontSize: 10
                        },
                        legend: {
                            display: true,
                            position: 'bottom',
                            labels:{
                                fontSize: 17,
                                fontColor: "white"
                            }
                        },
                        scales: {
                            yAxes: [{
                                ticks: {
                                    fontColor: "white",
                                    fontSize: 18,
                                    stepSize: 1,
                                    // beginAtZero: true
                                }
                            }],
                            xAxes: [{
                                ticks: {
                                    fontColor: "white",
                                    fontSize: 14,
                                    stepSize: 1,
                                    // beginAtZero: true
                                }
                            }]
                        }
                    }}
                />
            </div>
        </div>
    )
}
export default CoinGraph